@use "../../../style/var";
@use "../../../style/mixin";


.navLogged{
    position: fixed;
    top: 0;
    // left: 0;
    height: 81px; 
    display: flex;
    justify-content: space-between;
    background-color:var.$background-color;
    border-bottom: 1px solid rgb(151, 146, 146);
    width: 100%;


    button{
        background-color:var.$background-color;
        display: flex;
        align-items: center;

        .logo{
            margin-right :auto;
        }

        .logoIdea{
            width: 3rem;
            margin-left: 0.7rem;
            margin-right: 0.2rem;
            margin-bottom: 5%;
        }

        .logoSkillswap{
            // width: 70%;
            height: 4.5rem;
            margin-top: 0.4rem;
            margin-bottom: 0.1rem;         
        }
    }

    .logoUsers{
        display: flex;
        flex-direction: row;
        justify-content: end;
        align-items: center;
        margin-right: 0.7rem; 


        .logo_dollar{
            display: flex;
            flex-direction: row;
            align-items: center;
            border-radius: 100%;
            border: solid 1px var.$soft-grey-color;
            background-color:var.$yellow_color;  
            padding: 0.3rem 0.6rem;   
            color:var.$xtra-dark-grey;
            margin-right: 5px;
        }


        .swappies{
            font-size: 1.4rem;
            font-weight: 700;
            margin-right: 4px;
            color:var.$xtra-dark-grey;
        }

        .icone{
            // margin-right: 0.5rem; 
            color:var.$xtra-dark-grey;
            height:1.6rem; 
            // width: 18px;
            // padding: 0.5rem 0.5rem;
            
        }
    }
}


.nav_bottom {
    position: fixed;
    bottom: 0;
    width: 100%;
    background-color: var.$background-color;
    overflow: hidden;
    display: flex;
    justify-content: space-around;
    padding: 14px 0;
    border-top: 1px solid var.$xtra-soft-grey;

    a {
        display: flex;
        flex-direction: column;
        align-items: center;
        color: var.$xtra-dark-grey;
        font-size: 0.7rem;
        // padding: 14px 20px;
    }
        
    a:hover {
        color: var.$metal-blue;
    }

    .icone{
        height: 1.5rem;
        padding-bottom: 3px;
    }
}
    
    